


















































































































































































































































.phone_title {
    font-size: 16px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 22px;
}
.content {
    height: 100vh;
}
.phone_row {
    display: flex;
    margin-top: 23px;
}
.phone_box {
    width: 199px;
    height: 80px;

    box-sizing: border-box;
    padding: 10px;
    background: #ffffff;
    box-shadow: 0px 2px 16px 6px rgba(124, 124, 124, 0.1);
    border-radius: 2px;
}
.phone_box > div {
    display: flex;
    justify-content: space-between;
}
.phone_txt {
    font-size: 14px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 20px;
}
.phone_del {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #fb4702;
    line-height: 20px;
}
.phone_add {
    cursor: pointer;
    margin-left: 20px;
    width: 199px;
    font-size: 14px;
    line-height: 80px;
    text-align: center;
    height: 80px;
    background: #ffffff;
    border-radius: 2px;
    border: 1px dashed #e8e8e8;
}
.ver_code {
    display: inline-block;
    margin-left: 10px;
    text-align: center;
    width: 94px;
    height: 32px;
    line-height: 32px;
    background: #ffffff;
    border-radius: 2px;
    border: 1px solid #fb4702;
    span {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #fb4702;
    }
}
